import {
  JSONType,
  HttpMethod,
  TrackParametersType,
  IExternalRequestDispatcher,
} from '@kameleoon/javascript-sdk-core';

export class RequestDispatcher implements IExternalRequestDispatcher {
  public async track({
    url,
    headers,
    body,
  }: TrackParametersType): Promise<boolean> {
    const response = await fetch(url, {
      method: HttpMethod.Post,
      headers,
      body,
    });

    return response.ok;
  }

  public async get(url: string): Promise<JSONType> {
    const response = await fetch(url, {
      method: HttpMethod.Get,
    });

    return response.json();
  }
}
